@import "../../inc/vars";
.acrobits_layered-images {
	background: linear-gradient(180deg, #3A1F6E 0%, #40237A 33.29%, #5D4292 96.88%);
	padding: 60px 0;
	overflow: hidden;
	.title-box {
		padding: 0 30px;
		text-align: center;
		max-width: 100%;
		width: 1000px;
		margin: 0 auto;
		@media (min-width: $screen-sm){
			padding: 0 30px;
		}
	}
	&__title {
		color: #fff;
		@include pop(400, 35, 1.24);
		letter-spacing: -0.7px;
		margin-bottom: 24px;
		@media (min-width: $screen-sm){
			@include pop(400, 44, 1.12);
			letter-spacing: -0.88px;
			margin-bottom: 16px;
		}
	}
	&__description {
		@include fig(400, 20, 1.24);
		color: rgba(255, 255, 255, .8);
		text-align: center;
		@media (min-width: $screen-sm){
			line-height: 156%;
		}
	}
	&__images {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		position: relative;
		padding-top: 65px;
		@media (min-width: $screen-sm){
			padding-top: 130px;
		}
		img {
			max-width: 100%;
			height: auto;
		}
		.image1 {
			width: 756px;
			position: absolute;
			left: 50%;
			transform: translateX(-50%);
			bottom: calc(100% + 60px);
			transition: 1.2s;
			animation: layered-not-hover1 1.9s;
		}
		.image2 {
			width: 660px;
		}
		.image3 {
			width: 182px;
			position: absolute;
			left: 70%;
			transform: translateX(-50%);
			bottom: calc(100% + 60px);
			transition: .8s;
			animation-delay: 1s;
			animation: layered-not-hover2 1.9s;
		}
	}
	&.in-viewport {
		.acrobits_layered-images__images {
			.image1 {
				animation: layered-hover1 2.4s;
			}
			.image3 {
				animation: layered-hover2 2.4s;
			}
		}
	}
}
.acrobits_layered-images__images:hover {
	.image1 {
		bottom: 50px;
	}
	.image3 {
		bottom: 80%;
	}
}

@keyframes layered1 {
	0% {
		bottom: 50px;
	}
	50% {
		bottom: calc(100% + 60px);
	}
	100% {
		bottom: 50px;
	}
}
@keyframes layered2 {
	0% {
		bottom: 80%;
	}
	50% {
		bottom: calc(100% + 60px);
	}
	100% {
		bottom: 80%;
	}
}

@keyframes layered-not-hover1 {
	0% {
		bottom: 50px;
	}
	100% {
		bottom: calc(100% + 60px);
	}
}
@keyframes layered-not-hover2 {
	0% {
		bottom: 80%;
	}
	100% {
		bottom: calc(100% + 60px);
	}
}



@keyframes layered-hover1 {
	0% {
		bottom: calc(100% + 60px);
	}
	50% {
		bottom: 50px;
	}
	100% {
		bottom: calc(100% + 60px);
	}
}
@keyframes layered-hover2 {
	0% {
		bottom: calc(100% + 60px);
	}
	50% {
		bottom: 80%;
	}
	100% {
		bottom: calc(100% + 60px);
	}
}